import { ActionTree, GetterTree, Module, MutationTree } from "vuex";
import { RootState } from "../types";
import { VictoryState } from "./types";

const state: VictoryState = {
  VictoryName: [],
  AmuletNumber: 0,
  TheAmuletLeftOver: 0,
  Warning: false,
  disable10: false,
  disable1: false,
  hiddenvideo: true,
};
export enum VictoryGetter {
  VictoryName = "VictoryName",
  AmuletNumber = "AmuletNumber",
  TheAmuletLeftOver = "TheAmuletLeftOver",
  Warning = "Warning",
  disable10 = "disable10",
  disable1 = "disable1",
  hiddenvideo = "hiddenvideo",
}
const getters: GetterTree<VictoryState, RootState> = {
  [VictoryGetter.VictoryName](): any {
    return state.VictoryName;
  },
  [VictoryGetter.hiddenvideo](): any {
    return state.hiddenvideo;
  },
  [VictoryGetter.AmuletNumber](): any {
    return state.AmuletNumber;
  },
  [VictoryGetter.TheAmuletLeftOver](): any {
    return state.TheAmuletLeftOver;
  },
  [VictoryGetter.Warning](): any {
    return state.Warning;
  },
  [VictoryGetter.disable10](): any {
    return state.disable10;
  },
  [VictoryGetter.disable1](): any {
    return state.disable1;
  },
};
export enum VictoryMutations {
  GET_DATA = "GET_DATA",
  GET_AMULET = "GET_AMULET",
  CHECK_AMULET = "CHECK_AMULET",
  TOGGLE_VIDEO = "TOGGLE_VIDEO",
}
const mutations: MutationTree<VictoryState> = {
  [VictoryMutations.GET_DATA](state, payload: Array<string>) {
    state.VictoryName = state.VictoryName.concat(payload);
  },
  [VictoryMutations.GET_AMULET](state, payload: number) {
    state.AmuletNumber = payload;
  },

  [VictoryMutations.CHECK_AMULET](state, payload: number) {
    if (state.TheAmuletLeftOver + payload <= state.AmuletNumber) {
      state.TheAmuletLeftOver = state.TheAmuletLeftOver + payload;
    } else {
      if (payload == 1) {
        state.disable1 = true;
      }
      if (payload == 10) {
        state.disable10 = true;
      }

      setTimeout(() => {
        state.Warning = true;
      }, 500);
      setTimeout(() => {
        state.Warning = false;
      }, 3000);
    }
  },
  [VictoryMutations.TOGGLE_VIDEO](state, payload: boolean) {
    state.hiddenvideo = payload;
  },
};

const actions: ActionTree<VictoryState, RootState> = {
  getData({ commit }, value): void {
    commit("GET_DATA", value);
  },
  getAmulet({ commit }, value): void {
    commit("GET_AMULET", value);
  },
  checkAmulet({ commit }, value): void {
    commit("CHECK_AMULET", value);
  },
  toggleVideo({ commit }, value): void {
    commit("TOGGLE_VIDEO", value);
  },
};

export const victory: Module<VictoryState, RootState> = {
  state,
  getters,
  mutations,
  actions,
};
