








































































import { Component, Vue, Watch } from "vue-property-decorator";
import { mapGetters } from "vuex";
import { Action, Getter } from "vuex-class";

@Component({
  computed: {
    ...mapGetters({ VictoryName: "VictoryName" }),
  },
})
export default class App extends Vue {
  VictoryName!: Array<string>;
  show: boolean = true;
  hidden: boolean = true;
  switch1: boolean = true;
  counter: number = 0;
  alert: boolean = false;
  hadWarning: boolean = false;
  @Getter
  AmuletNumber!: number;
  @Getter TheAmuletLeftOver!: number;
  @Getter Warning!: boolean;
  @Action
  getAmulet(val: number) {
    this.getAmulet(val);
  }
  @Action("toggleVideo")
  toggleVideo(val: any) {
    this.toggleVideo(val);
  }
  rnd: number = Math.floor(Math.random() * 1000) + 5;
  created() {
    setTimeout(() => {
      this.alert = true;
    }, 500);
    setTimeout(() => {
      this.alert = false;
    }, 3000);
    this.getAmulet(this.rnd);
  }
  @Watch("VictoryName")
  countSSR() {
    this.counter = this.VictoryName.length;
  }
  @Watch("$route")
  hiddenSRR() {
    let link = this.$route.path.replace("/", "");
    if (link == "about") {
      this.hidden = false;
    } else {
      this.hidden = true;
    }
  }
  @Watch("Warning")
  checkWarning() {
    this.hadWarning = this.Warning;
  }
  @Watch("switch1")
  wSwitch1() {
    this.toggleVideo(this.switch1);
  }
}
